* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  margin: auto;
  height: 100vh;
  font-family: 'Fluro';
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

@font-face {
  font-family: 'FLURO';
  src: url('./fonts/FLUROLight.eot');
  src: url('./fonts/FLUROLight.eot?#iefix') format('embedded-opentype'),
    url('./fonts/FLUROLight.woff2') format('woff2'),
    url('./fonts/FLUROLight.woff') format('woff'),
    url('./fonts/FLUROLight.ttf') format('truetype'),
    url('./fonts/FLUROLight.svg#FLUROLight') format('svg');
  font-weight: 300;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'FLURO';
  src: url('./fonts/FLUROBold.eot');
  src: url('./fonts/FLUROBold.eot?#iefix') format('embedded-opentype'),
    url('./fonts/FLUROBold.woff2') format('woff2'),
    url('./fonts/FLUROBold.woff') format('woff'),
    url('./fonts/FLUROBold.ttf') format('truetype'),
    url('./fonts/FLUROBold.svg#FLUROBold') format('svg');
  font-weight: bold;
  font-style: normal;
  font-display: swap;
}

